@charset "utf-8";

@import "_breakPoints.scss";
@import "_variables.scss";

@mixin mq($breakPoint) {
    @media #{map-get($breakPoints, $breakPoint)} {
        @content;
    }
}

@mixin rgb-text-field-base() {
    padding: 4px;
    margin-right: 16px;
    width: 48px;
    border-radius: 4px;
    background-color: $white;
    font-weight: normal;
}
